<template>
	<view class="bgm">
		<view class="head">
			<view class="Size">忘记密码</view>
		</view>
		<view class="body">
			<view class="input_box">
				<view class="top_box">验证手机号</view>
				<view class="bottom_box">
					<view class="left">
						<u-icon size="20" name="/static/img/19.png" class="footIcon" v-if="iconshow"></u-icon>
						<u-icon size="20" name="/static/img/icon_phone.png" class="footIcon" v-else></u-icon>
					</view>
					<view class="center">
						<u-input type="number" placeholder="请输入手机号" v-model="form.phone" @input="onInput" />
					</view>
					<view class="left">
						<u-icon size="20" name="/static/img/4.png" class="footIcon" v-if="iconshow"></u-icon>
						<u-icon size="20" name="/static/img/4s.png" class="footIcon" v-else></u-icon>
					</view>
				</view>
			</view>
			<view class="input_box">
				<view class="top_box">验证码</view>
				<view class="bottom_box">
					<view class="left">
						<u-icon size="20" name="/static/img/21.png" class="footIcon" v-if="iconshow2"></u-icon>
						<u-icon size="20" name="/static/img/20s.png" class="footIcon" v-else></u-icon>
					</view>
					<view class="center">
						<u-input type="text" placeholder="请输入验证码" v-model="form.code" @input="onInput2" />
					</view>
					<view class="left">
						<u-icon size="20" name="/static/img/4.png" class="footIcon" v-if="iconshow2"></u-icon>
						<u-icon size="20" name="/static/img/4s.png" class="footIcon" v-else></u-icon>
					</view>
				</view>
			</view>
			<view class="input_box">
				<view class="top_box">重置密码</view>
				<view class="bottom_box">
					<view class="left">
						<u-icon size="20" name="/static/img/20.png" class="footIcon" v-if="iconshow3"></u-icon>
						<u-icon size="20" name="/static/img/17.png" class="footIcon" v-else></u-icon>
					</view>
					<view class="center">
						<u-input type="password" placeholder="请输入新的密码" v-model="form.pwd" @input="onInput3" />
					</view>
					<view class="left">
						<u-icon size="20" name="/static/img/4.png" class="footIcon" v-if="iconshow3"></u-icon>
						<u-icon size="20" name="/static/img/4s.png" class="footIcon" v-else></u-icon>
					</view>
				</view>
			</view>
			<view class="input_box">
				<view class="top_box">重新密码</view>
				<view class="bottom_box">
					<view class="left">
						<u-icon size="20" name="/static/img/20.png" class="footIcon" v-if="iconshow4"></u-icon>
						<u-icon size="20" name="/static/img/17.png" class="footIcon" v-else></u-icon>
					</view>
					<view class="center">
						<u-input type="password" placeholder="请再次输入密码" v-model="form.pwds" @input="onInput4" />
					</view>
					<view class="left">
						<u-icon size="20" name="/static/img/4.png" class="footIcon" v-if="iconshow4"></u-icon>
						<u-icon size="20" name="/static/img/4s.png" class="footIcon" v-else></u-icon>
					</view>
				</view>
			</view>
			<view class="btns">
				<button type="default" class="btn_primary" :disabled="disabled" @click="submit">完成</button>
			</view>
		</view>
		<u-toast ref="uToast" />
	</view>
</template>

<script>
	export default {
		data() {
			return {
				iconshow:false,
				iconshow2:false,
				iconshow3:false,
				iconshow4:false,
				disabled:true,
				form:{
					phone:"",
					code:"",
					pwd:"",
					pwds:""
				}
			}
		},
		methods: {
			onInput(e){
				if(e!=""){
					this.iconshow = true;
				}else{
					this.iconshow = false;
				}
				this.onshow();
			},
			onInput2(e){
				if(e!=""){
					this.iconshow2 = true;
				}else{
					this.iconshow2 = false;
				}
				this.onshow();
			},
			onInput3(e){
				if(e!=""){
					this.iconshow3 = true;
				}else{
					this.iconshow3 = false;
				}
				this.onshow();
			},
			onInput4(e){
				if(e!=""){
					this.iconshow4 = true;
				}else{
					this.iconshow4 = false;
				}
				this.onshow();
			},
			onshow(){
				if(this.form.phone!="" && this.form.code!="" && this.form.pwd!="" && this.form.pwds!=""){
					this.disabled = false;
				}else{
					this.disabled = true;
				}
			},
			submit(){
				if(this.form.phone==""){
					this.$refs.uToast.show({
						title: '请输入手机号'
					})
					return false;
				}
				if(this.form.code==""){
					this.$refs.uToast.show({
						title: '请输入验证码'
					})
					return false;
				}
				if(this.form.pwd==""){
					this.$refs.uToast.show({
						title: '请输入新密码'
					})
					return false;
				}
				if(this.form.pwds==""){
					this.$refs.uToast.show({
						title: '请再次输入新密码'
					})
					return false;
				}
				this.$refs.uToast.show({
					title: '保存成功',
					url: '/pages/login/login'
				})
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: transparent;
		height: 100%;
	}
.bgm{
	width: 100%;
	height: 100%;
	background: url(../../static/img/167.png) no-repeat;
	background-size: 100% 100%;
	.head{
		height: 21%;
		position: relative;
		margin-bottom:100rpx;
		.Size{
			position: absolute;
			left: 65rpx;
			bottom: 0;
			font-weight: 500;
			font-size: 50rpx;
		}
	}
	.body{
		margin:0 9%;
		.input_box{
			margin-top:40rpx;
			.top_box{
				height: 20px;
				font-size: 24rpx;
				color: #D4D4D4;
			}
			.bottom_box{
				display: flex;
				align-items: center;
				height: 80rpx;
				border-bottom: 2rpx solid #E6E6E6;
				.left{
					display: flex;
					padding:0 28rpx;
				}
				.center{
					flex: 1;
					padding-right:20rpx;
					input{
						color:#707070;
						font-size: 28rpx;
					}
				}
			}
		}
		.btns{
			position: fixed;
			bottom:132rpx;
			left:0;
			z-index: 50;
			width:100%;
			.btn_primary{
				margin:0 120rpx;
				height:92rpx;
				line-height: 92rpx;
				border-radius: 10rpx;
				box-shadow: 0px 1px 2px 0px rgba(0,0,0,0.16);
				font-size: 28rpx;
				color:#ffffff;
				background:#FF9D00;
				&[disabled]:not([type]), &[disabled][type=default]{
					background: #d4d4d4;
				}
			}
		}
	}
}
</style>
